/*error notic*/
.error-notice{
    border: 1px solid #d8345f;
    background-color: #e79cc2;
    padding: 0.5rem;
    border-radius: 8px;
    margin: 1rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.error-notice button{
    background-color: #d8345f;
    height: 20px;
    width: 20px;
    border-radius: 50%;
}

